@use '../abstracts';

body {
    font-family: 'Nunito';
}
h1 {
    font-family: 'Fredoka One';
    font-size: 2.2em;
}
h2, h3 {
    font-family: 'Nunito';
    line-height: 1.1;
    margin-top: 0;
    font-weight: 800;
}

h2 {
    font-style: 1.5em;
}

h3 {
    font-style: 1.25em;
}

p {
    margin-top: 0;
    font-size: 1.0625em;
}

a, a:visited, a:active {
    text-decoration: none;
}

ul, ol, ol li, ul li {
    color: abstracts.$noir;
    padding: 0;
    margin: 0;
    list-style-type: none;
}